<template>
  <div>
    <!-- 表格数据 -->
    <el-table ref="filterTable" :data="userList" style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="教师编号">
              <span>{{ scope.row.t_id }}</span>
            </el-form-item>
            <el-form-item label="教师姓名">
              <span>{{ scope.row.t_name }}</span>
            </el-form-item>
            <el-form-item label="教授课程">
              <span>{{ scope.row.course }}</span>
            </el-form-item>
            <el-form-item label="入职时间" width="180"
              ><span>{{ scope.row.t_date }}</span></el-form-item
            >
            <el-form-item label="家庭地址">
              <span>{{ scope.row.t_address }}</span>
            </el-form-item>
            <el-form-item label="信息说明" width="180"
              ><span>{{ scope.row.t_title }}</span></el-form-item
            >
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="Id" label="ID" width="180"> </el-table-column>
      <el-table-column prop="t_id" label="教师编号" width="180">
      </el-table-column>
      <el-table-column prop="t_name" label="教师姓名" width="180">
      </el-table-column>

      <!-- 设置el-tag -->
      <el-table-column
        prop="course"
        sortable
        label="课程"
        width="100"
        :filters="selectCourse"
        :filter-method="filterHandler"
        filter-placement="bottom-end"
      >
      <!-- <el-tag>{{selectCourse[0].text}}</el-tag> -->

        <template slot-scope="scope">
          <el-tag v-if="scope.row.value === selectCourse[0].text">{{
            scope.row.course
          }}</el-tag>
          <el-tag
            type="success"
            v-else-if="scope.row.course === selectCourse[1].text"
            >{{ scope.row.course }}</el-tag
          >
          <el-tag
            type="warning"
            v-else-if="scope.row.course === selectCourse[2].text"
            >{{ scope.row.course }}</el-tag
          >
          <el-tag type="danger" v-else>{{ scope.row.course }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.t_id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfo.pageIndex"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageInfo.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isEdit: false,
      input: '',
      userList: [],
      total: 0,
      pageInfo: {
        pageIndex: 1,
        pageSize: 10
      },
      selectCourse: []
    }
  },
  methods: {
    filterHandler (value, row, column) {
      const property = column.property
      return row[property] === value
    },
    // 获取全部数据
    getAllList () {
      this.axios.get('/api/getAll/TeacherInfo').then((res) => {
        const array = res.data.list
        const m1 = new Map()
        const unique1 = (array, key) => {
          for (const item of array) {
            if (!m1.has(item[key])) {
              m1.set(item[key], item)
            }
          }
          return [...m1.values()]
        }
        const a1 = unique1(array, 'course')
        for (let i = 0; i < a1.length; i++) {
          this.selectCourse.push({ value: a1[i].course, text: a1[i].course })
        }
      })
    },
    // 分页获取数据
    getUserList () {
      this.axios
        .get('/api/getByPage/teacherInfo', {
          params: this.pageInfo
        })
        .then((res) => {
          this.userList = res.data.list
          this.total = res.data.total
        })
    },

    handleDelete (id) {
      this.axios
        .post('/api/del/TeaInfo', {
          id
        })
        .then((res) => {
          if (
            (res.data.total / 10) % 1 === 0 &&
            this.pageInfo.pageIndex === res.data.total / 10 + 1
          ) {
            this.pageInfo.pageIndex = this.pageInfo.pageIndex - 1
            this.getUserList()
          }
          this.getUserList()
        })
    },
    // 分页 handleSizeChange() handleCurrentChange()
    // 监听 pageSizeChange
    handleSizeChange (newSize) {
      this.pageInfo.pageSize = newSize
      this.getUserList()
    },
    // 监听 pagenum改变
    handleCurrentChange (newPage) {
      this.pageInfo.pageIndex = newPage
      this.getUserList()
    }
  },
  created () {
    this.getAllList()
    this.getUserList()
  }
}
</script>

<style lang="less">
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 100px;
  width: 35%;
}
</style>
